import {isConvertible} from './isConvertible';

/**
 * This is a class that implement a interface
 */
export class Volumen implements isConvertible {
    constructor(public unidad: number) {

    }
    help() {
        console.log("Cree un objeto con la fuerza en litros y con la función convertir se le devolverá en mililitros");
    }

    convertir() {
        return this.unidad * 1000;
    }
}